@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  font-family: Poppins, sans-serif;
}

/*
* @Loader
*/

.md {
  .h-fullscreen {
    height: 100% !important;
  }
}

.font-editor {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

:root {
  --hue: 223;
  --bg: hsl(var(--hue), 90%, 90%);
  --fg: hsl(var(--hue), 90%, 10%);
  --primary: hsl(var(--hue), 90%, 50%);
  --trans-dur: 0.3s;
}

.loading {
  color: var(--fg);
  display: flex;
  font: 1em/1.5 sans-serif;
  height: 100vh;
  width: 100vw;
  transition:
    background-color var(--trans-dur),
    color var(--trans-dur);
  .bike {
    display: block;
    margin: auto;
    width: 16em;
    height: auto;
  }
  .bike__body,
  .bike__front,
  .bike__handlebars,
  .bike__pedals,
  .bike__pedals-spin,
  .bike__seat,
  .bike__spokes,
  .bike__spokes-spin,
  .bike__tire {
    animation: bikeBody 3s ease-in-out infinite;
    stroke: var(--primary);
    transition: stroke var(--trans-dur);
  }
  .bike__front {
    animation-name: bikeFront;
  }
  .bike__handlebars {
    animation-name: bikeHandlebars;
  }
  .bike__pedals {
    animation-name: bikePedals;
  }
  .bike__pedals-spin {
    animation-name: bikePedalsSpin;
  }
  .bike__seat {
    animation-name: bikeSeat;
  }
  .bike__spokes,
  .bike__tire {
    stroke: currentColor;
  }
  .bike__spokes {
    animation-name: bikeSpokes;
  }
  .bike__spokes-spin {
    animation-name: bikeSpokesSpin;
  }
  .bike__tire {
    animation-name: bikeTire;
  }

  /* Dark theme */
  @media (prefers-color-scheme: dark) {
    :root {
      --bg: hsl(var(--hue), 90%, 10%);
      --fg: hsl(var(--hue), 90%, 90%);
    }
  }

  /* Animations */
  @keyframes bikeBody {
    from {
      stroke-dashoffset: 79;
    }
    33%,
    67% {
      stroke-dashoffset: 0;
    }
    to {
      stroke-dashoffset: -79;
    }
  }
  @keyframes bikeFront {
    from {
      stroke-dashoffset: 19;
    }
    33%,
    67% {
      stroke-dashoffset: 0;
    }
    to {
      stroke-dashoffset: -19;
    }
  }
  @keyframes bikeHandlebars {
    from {
      stroke-dashoffset: 10;
    }
    33%,
    67% {
      stroke-dashoffset: 0;
    }
    to {
      stroke-dashoffset: -10;
    }
  }
  @keyframes bikePedals {
    from {
      animation-timing-function: ease-in;
      stroke-dashoffset: -25.133;
    }
    33%,
    67% {
      animation-timing-function: ease-out;
      stroke-dashoffset: -21.991;
    }
    to {
      stroke-dashoffset: -25.133;
    }
  }
  @keyframes bikePedalsSpin {
    from {
      transform: rotate(0.1875turn);
    }
    to {
      transform: rotate(3.1875turn);
    }
  }
  @keyframes bikeSeat {
    from {
      stroke-dashoffset: 5;
    }
    33%,
    67% {
      stroke-dashoffset: 0;
    }
    to {
      stroke-dashoffset: -5;
    }
  }
  @keyframes bikeSpokes {
    from {
      animation-timing-function: ease-in;
      stroke-dashoffset: -31.416;
    }
    33%,
    67% {
      animation-timing-function: ease-out;
      stroke-dashoffset: -23.562;
    }
    to {
      stroke-dashoffset: -31.416;
    }
  }
  @keyframes bikeSpokesSpin {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(3turn);
    }
  }
  @keyframes bikeTire {
    from {
      animation-timing-function: ease-in;
      stroke-dashoffset: 56.549;
      transform: rotate(0);
    }
    33% {
      stroke-dashoffset: 0;
      transform: rotate(0.33turn);
    }
    67% {
      animation-timing-function: ease-out;
      stroke-dashoffset: 0;
      transform: rotate(0.67turn);
    }
    to {
      stroke-dashoffset: -56.549;
      transform: rotate(1turn);
    }
  }
}

/*
 * @Auth
*/

.limiter {
  width: 100%;
  margin: 0 auto;
  * {
    transition: all 250ms ease;
    font-family: Poppins;
  }
  .container-login100 {
    width: 100%;
    min-height: 100vh;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 15px;
    background: #f2f2f2;

    .wrap-login100 {
      width: 390px;
      background: #fff;
      border-radius: 10px;
      overflow: hidden;
      padding: 77px 55px 33px 55px;

      box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
      -moz-box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
      -webkit-box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
      -o-box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
      -ms-box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);

      .login100-form {
        width: 100%;

        .login100-form-title {
          display: block;
          font-weight: 600;
          font-size: 30px;
          color: #333333;
          line-height: 1.2;
          text-align: center;

          i {
            font-size: 60px;
          }
        }

        .wrap-input100 {
          width: 100%;
          position: relative;
          border-bottom: 2px solid #adadad;
          margin-bottom: 37px;

          .btn-show-pass {
            font-size: 15px;
            color: #999999;

            display: -webkit-box;
            display: -webkit-flex;
            display: -moz-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            position: absolute;
            height: 100%;
            top: 0;
            right: 0;
            padding-right: 5px;
            cursor: pointer;
            -webkit-transition: all 0.4s;
            -o-transition: all 0.4s;
            -moz-transition: all 0.4s;
            transition: all 0.4s;

            &:hover {
              color: #6a7dfe;
              color: -webkit-linear-gradient(to right, #21d4fd, #b721ff);
              color: -o-linear-gradient(to right, #21d4fd, #b721ff);
              color: -moz-linear-gradient(to right, #21d4fd, #b721ff);
              color: linear-gradient(to right, #21d4fd, #b721ff);
            }
          }

          .btn-show-pass.active {
            color: #6a7dfe;
            color: -webkit-linear-gradient(to right, #21d4fd, #b721ff);
            color: -o-linear-gradient(to right, #21d4fd, #b721ff);
            color: -moz-linear-gradient(to right, #21d4fd, #b721ff);
            color: linear-gradient(to right, #21d4fd, #b721ff);
          }

          input {
            outline: none;
            border: none;
          }

          .input100 {
            font-size: 15px;
            color: #555555;
            line-height: 1.2;

            display: block;
            width: 100%;
            height: 45px;
            background: white;
            padding: 0 5px;
          }

          .focus-input100 {
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            pointer-events: none;
            z-index: 0;

            &::before {
              content: "";
              display: block;
              position: absolute;
              bottom: -2px;
              left: 0;
              width: 0;
              height: 2px;

              -webkit-transition: all 0.4s;
              -o-transition: all 0.4s;
              -moz-transition: all 0.4s;
              transition: all 0.4s;

              background: #6a7dfe;
              background: -webkit-linear-gradient(to right, #21d4fd, #b721ff);
              background: -o-linear-gradient(to right, #21d4fd, #b721ff);
              background: -moz-linear-gradient(to right, #21d4fd, #b721ff);
              background: linear-gradient(to right, #21d4fd, #b721ff);
            }

            &::after {
              font-size: 15px;
              color: #999999;
              line-height: 1.2;

              content: attr(data-placeholder);
              display: block;
              width: 100%;
              position: absolute;
              top: 16px;
              left: 0px;
              padding-left: 5px;

              -webkit-transition: all 0.4s;
              -o-transition: all 0.4s;
              -moz-transition: all 0.4s;
              transition: all 0.4s;
            }
          }

          .has-val::after,
          .input100:focus + .focus-input100::after {
            top: -15px !important;
          }

          .has-val-input100::before,
          .input100:focus + .focus-input100::before {
            width: 100% !important;
          }
        }

        .container-login100-form-btn {
          display: -webkit-box;
          display: -webkit-flex;
          display: -moz-box;
          display: -ms-flexbox;
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          padding-top: 13px;

          .wrap-login100-form-btn {
            width: 100%;
            display: block;
            position: relative;
            z-index: 1;
            border-radius: 25px;
            overflow: hidden;
            margin: 0 auto;

            button {
              outline: none !important;
              border: none;
              background: transparent;
              &:hover {
                cursor: pointer;
              }
            }

            .login100-form-btn {
              font-size: 15px;
              color: #fff;
              line-height: 1.2;
              text-transform: uppercase;

              display: -webkit-box;
              display: -webkit-flex;
              display: -moz-box;
              display: -ms-flexbox;
              display: flex;
              justify-content: center;
              align-items: center;
              padding: 0 20px;
              width: 100%;
              height: 50px;
            }
          }
          .login100-form-bgbtn {
            position: absolute;
            z-index: -1;
            width: 300%;
            height: 100%;
            background: #a64bf4;
            background: -webkit-linear-gradient(
              to left,
              #21d4fd,
              #b721ff,
              #21d4fd,
              #b721ff
            );
            background: -o-linear-gradient(
              to left,
              #21d4fd,
              #b721ff,
              #21d4fd,
              #b721ff
            );
            background: -moz-linear-gradient(
              to left,
              #21d4fd,
              #b721ff,
              #21d4fd,
              #b721ff
            );
            background: linear-gradient(
              to left,
              #21d4fd,
              #b721ff,
              #21d4fd,
              #b721ff
            );
            top: 0;
            left: -100%;

            -webkit-transition: all 0.4s;
            -o-transition: all 0.4s;
            -moz-transition: all 0.4s;
            transition: all 0.4s;
          }

          .wrap-login100-form-btn:hover .login100-form-bgbtn {
            left: 0;
          }
        }

        .p-b-26 {
          padding-bottom: 26px;
        }
        .p-b-48 {
          padding-bottom: 48px;
        }
        .p-t-115 {
          padding-top: 115px;
        }
        .text-center {
          text-align: center;
        }

        .txt1 {
          font-size: 13px;
          color: #666666;
          line-height: 1.5;
        }

        .txt2 {
          font-size: 13px;
          color: #333333;
          line-height: 1.5;
          cursor: pointer;
          text-decoration: underline;
        }
      }
    }
  }
  @media (max-width: 576px) {
    .wrap-login100 {
      padding: 77px 15px 33px 15px;
    }
  }
}

